import produce from 'immer';

const INITIAL_STATE = {
  services: null,
  loading: false,
};
export default function services(state = INITIAL_STATE, action) {
  return produce(state, draft => {
    switch (action.type) {
      case '@service/INSERT_SERVICE_REQUEST': {
        draft.services = action.payload.services;
        break;
      }
      case '@service/INSERT_SERVICE_REQUEST_FAILURE': {
        draft.loading = false;
        break;
      }
      case '@service/INSERT_SERVICE_REQUEST_SUCCESS': {
        draft.services = action.payload.services;
        draft.loading = false;
        break;
      }
      default:
    }
  });
}
