import React from 'react';
import { useDispatch } from 'react-redux';
import { Link } from 'react-router-dom';
import { Form, Input } from '@rocketseat/unform';
import * as Yup from 'yup';
import MenuItem from '@material-ui/core/MenuItem';
import Select from '@material-ui/core/Select';
import { rgba } from 'polished';
import { MdNoEncryption } from 'react-icons/md';
import DateFnsUtils from '@date-io/date-fns';
import {
    MuiPickersUtilsProvider,
    KeyboardTimePicker,
    KeyboardDatePicker,
} from '@material-ui/pickers';
import { withStyles } from '@material-ui/core/styles';
import { green } from '@material-ui/core/colors';
import FormGroup from '@material-ui/core/FormGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import CheckBoxOutlineBlankIcon from '@material-ui/icons/CheckBoxOutlineBlank';
import CheckBoxIcon from '@material-ui/icons/CheckBox';
import Favorite from '@material-ui/icons/Favorite';
import FavoriteBorder from '@material-ui/icons/FavoriteBorder';
import { signUpRequest } from '~/store/modules/auth/actions';
import logo from '~/assets/logo.svg';
import { is } from 'date-fns/locale';
import { toast } from 'react-toastify';

const schema = Yup.object().shape({
    nome: Yup.string().required('O nome é obrigatório'),
    email: Yup.string()
        .email('Insira um email válido')
        .required('O email é obrigatório'),
    senha: Yup.string()
        .min(6, 'No mínimo 6 caracteres')
        .required('A senha é obrigatória'),
    senhaConfirmacao: Yup.string()
        .min(6, 'No mínimo 6 caracteres')
        .required('A senha é obrigatória'),
});
const GreenCheckbox = withStyles({
    root: {
        color: green[400],
        '&$checked': {
            color: green[600],
        },
    },
    checked: {},
})(props => <Checkbox color="default" {...props} />);
// import { Container } from './styles';
export default function SignUp() {
    const dispatch = useDispatch();
    const [salao, setSalao] = React.useState(false);
    const [type, setType] = React.useState('cliente');
    const [cnpj, setCNPJ] = React.useState('');
    const [telefone, setTelefone] = React.useState('');
    const [open, setOpen] = React.useState(false);
    const [termos, setTermos] = React.useState( true );



    function handleSubmit({ nome, email, senha, senhaConfirmacao }) {
        if(termos){
            dispatch(
                signUpRequest(
                    nome,
                    email,
                    senha,
                    senhaConfirmacao,
                    telefone
                        .replace('(', '')
                        .replace(')', '')
                        .replace('-', '')
                        .replace(' ', ''),
                    cnpj,
                    type,termos
                )
            );
        }
            else {
                toast.error('Para concluir o cadastro é necessário aceitar os termos uso');
            }



        console.tron.log({ nome, email, senha, senhaConfirmacao });
    }
    const handleClose = () => {
        setOpen(false);
    };
    const handleChange = event => {
        if (event.target.value == 'dono') {
            setSalao(true);
        } else {
            setSalao(false);
        }
        setType(event.target.value);
    };
    const handleChangePhone = event => {
        let v = event.target.value;
        if (v.length < 16) {
            v = v.replace(/\D/g, ''); // Remove tudo o que não é dígito
            v = v.replace(/^(\d{2})(\d)/g, '($1) $2'); // Coloca parênteses em volta dos dois primeiros dígitos
            v = v.replace(/(\d)(\d{4})$/, '$1-$2');
            setTelefone(v);
        } else {
            setTelefone(telefone);
        }
    };
    const handleChangeCNPJ = event => {
        let v = event.target.value;
        if (v.length <= 18) {
            v = v.replace(/\D/g, '');
            v = v.replace(/^(\d{2})(\d)/, '$1.$2');
            v = v.replace(/^(\d{2})\.(\d{3})(\d)/, '$1.$2.$3');
            v = v.replace(/\.(\d{3})(\d)/, '.$1/$2');
            v = v.replace(/(\d{4})(\d)/, '$1-$2');
            setCNPJ(v);
        } else {
            setCNPJ(cnpj);
        }
    };


    const handleOpen = () => {
        setOpen(true);
    };
    const [selectedDate, setSelectedDate] = React.useState(
        new Date('2014-08-18T21:11:54')
    );
    const handlesChange2 = event => {
        setTermos( event.target.checked );
      };

    return (
        <>
            <img src={logo} alt="Barber" />

            <Form schema={schema} onSubmit={handleSubmit}>
                <Select
                    style={{
                        color: 'white',
                        background: rgba(0, 0, 0, 0.1),
                        marginBottom: 10,
                        borderRadius: 4,
                        border: 'none',
                    }}
                    labelId="demo-controlled-open-select-label"
                    id="demo-controlled-open-select"
                    open={open}
                    onClose={handleClose}
                    onOpen={handleOpen}
                    value={type}
                    onChange={handleChange}
                >
                    <MenuItem value="dono">Prestador de Serviço</MenuItem>
                    <MenuItem value="cliente">Cliente</MenuItem>
                </Select>
                {salao ? (
                    <>
                        <Input name="nome" placeholder="Nome comercial" />
                        <Input
                            value={cnpj}
                            onChange={e => handleChangeCNPJ(e)}
                            name="cnpj"
                            placeholder="CNPJ"
                        />
                    </>
                ) : (
                    <Input name="nome" placeholder="Nome completo" />
                )}

                <Input
                    name="telefone"
                    value={telefone}
                    onChange={e => handleChangePhone(e)}
                    placeholder="Telefone"
                />

                <Input name="email" type="email" placeholder="Seu email" />
                <Input name="senha" type="password" placeholder="Sua senha" />
                <Input
                    name="senhaConfirmacao"
                    type="password"
                    placeholder="Sua senha"
                />

                  <div style={{flexDirection:'row'}}>
                        <Checkbox
                            checked={termos}
                            onChange={handlesChange2}
                            value="checkedB"
                            color="primary"
                        />

                   <Link to="/termos">  <h4>TERMOS DE USO</h4></Link>
                   </div>

                <button type="submit">Criar conta</button>
                <Link to="/">Já tenho login</Link>
            </Form>
        </>
    );
}
