export function insertServiceRequest(services) {
  return {
    type: '@service/INSERT_SERVICE_REQUEST',
    payload: {services},
  };
}
export function insertServiceRequestFailure() {
  return {
    type: '@service/INSERT_SERVICE_REQUEST_FAILURE',
  };
}
export function insertServiceRequestSuccess(services) {
  return {
    type: '@service/INSERT_SERVICE_REQUEST_SUCCESS',
    payload: {services},
  };
}
