import { all, takeLatest, call, put } from 'redux-saga/effects';
// import { toast } from 'react-toastify';
import api from '~/services/api';
import { toast } from 'react-toastify';

import {
  insertServiceRequest,
  insertServiceRequestSuccess,
  insertServiceRequestFailure,
} from './actions';

export function* insertService({ payload }) {
  try {
    const { nome, preco, descricao } = payload.services;
    const response = yield call(api.post, 'services', { nome, preco, descricao });
    if (response.data) {
      // Alert.alert('Sucesso', 'Serviço cadastrado!!');
      toast.error(response.data.message);
      yield put(insertServiceRequestSuccess(response.data));
    } else {
      //('Erro', 'Confira os dados inseridos');
      toast.error('Erro ao atualizar o perfil, confira seus dados!');
      yield put(insertServiceRequestFailure());
    }
  } catch (err) {
    // Alert.alert('Erro', 'Confira os dados inseridos');
    toast.error('Erro ao atualizar o perfil, confira seus dados!');
    yield put(insertServiceRequestFailure());
  }
}

export default all([
  takeLatest('@service/INSERT_SERVICE_REQUEST', insertService),
]);
