import React, { useState , useMemo } from 'react';
import '../Home/styles.css';
import './styles.css';
import { Content } from '../Home/styles';
import { Container } from './styles';
import logo from '~/assets/logo.svg';
import camera from '~/assets/camera.svg';
// import { Container } from './styles';
import api from '~/services/api';

export default function New({history}) {
const [salao,setSalao] = useState('');
const [thumbnail,setThumbnail] = useState(null);
const [profissionais,setProfissionais] = useState('');
const [precos,setPrecos] = useState('');
const preview = useMemo(()=>{
    return thumbnail ? URL.createObjectURL(thumbnail) : null;
},[thumbnail])
async function handleSubmit(){
const data = new FormData();
data.append('thumbnail',thumbnail);
data.append('salao',salao);
data.append('precos',precos);
data.append('profissionais',profissionais);
const response = await api.post('/endpoint',data);
history.push('/dashboard');
    }
  return (
    <Container>
    <Content>
    <form onSubmit={handleSubmit}>
        <label id="thumbnail"
         style={{backgroundImage:`url(${preview})`}}
         className={thumbnail ?'has-thumbnail':''}
         >
            <input type="file" onChange={event => setThumbnail(event.target.files[0])}/>
            <img src={camera} alt="Select img"/>
        </label>
        <label htmlFor="salao">SALÃO DE BELEZA *</label>
        <input id="salao" placeholder="Seu salão de beleza"
        value={salao}
        onChange={event => setSalao(event.target.value)}
        />
        <label htmlFor="profissionais">PROFISSIONAIS *</label>
        <input id="profissionais" placeholder="Quais profissionais trabalham no salão?"
        value={profissionais}
        onChange={event => setProfissionais(event.target.value)}
        />
        <label htmlFor="precos">PREÇOS *</label>
        <input id="precos" placeholder="Quais são os preços?"
        value={precos}
        onChange={event => setPrecos(event.target.value)}
        />
        <button type="submit" className="btn">Cadastrar</button>

    </form>
    </Content>
        </Container>
  );
}
